import React, { Component, createContext, useContext } from 'react';

import Axios from "axios"
import { getApiLink } from "../lib/env.lib"

import notyf from "../lib/notification";
import { StorageContext } from "./storage_context"

export const AuthContext = createContext();

const cachify = true
function setInitialAuthState() {

    let auth = null

    if (cachify) {
        auth = window.localStorage.getItem("auth");
    }
    let Auth;

    // let { setItem, getItem, getStorage } = useContext(StorageContext)
    if (auth == null || auth === undefined || auth === "") {
        Auth = {
            AuthState: false,
            auth_token: "",
            user_id: "",
            user_email: "",
            user_email_verified: 0,
            user_fullname: "",
            user_username: "",
            user_dp: "",
            user_bio: "",
            user_account_type: 0,
            user_gender: -1,
        }
    } else {
        auth = JSON.parse(auth)
        Auth = {
            AuthState: (auth.auth_token === "") ? false : true,
            auth_token: auth.auth_token,
            user_id: auth.user_id,
            user_email: auth.user_email,
            user_email_verified: auth.user_email_verified,
            user_fullname: auth.user_fullname,
            user_username: auth.user_username,
            user_dp: auth.user_dp,
            user_bio: auth.user_bio,
            user_account_type: auth.user_account_type,
            user_gender: auth.user_gender,
        }
    }

    if (cachify) {
        window.localStorage.setItem("auth", JSON.stringify(Auth))
        window.localStorage.setItem("cache-update", Date.now())
    }
    // setItem()
    return Auth
}

class AuthContextProvider extends Component {

    static contextType = StorageContext

    constructor(props) {
        super(props);
        let auth = setInitialAuthState()

        this.state = {
            isLoggedIn: auth.AuthState,
            auth_token: auth.auth_token,
            user_id: auth.user_id,
            user_email: auth.user_email,
            user_email_verified: auth.user_email_verified,
            user_fullname: auth.user_fullname,
            user_username: auth.user_username,
            user_dp: auth.user_dp,
            user_bio: auth.user_bio,
            user_account_type: auth.user_account_type,
            user_gender: auth.user_gender,
        }

        this.setAuth = this.setAuth.bind(this)
        this.removeAuth = this.removeAuth.bind(this)

    }

    componentDidUpdate() {
        this.saveAuth()
    }

    saveAuth() {
        // save to secure storage
        let Auth = {
            AuthState: this.state.AuthState,
            auth_token: this.state.auth_token,
            user_id: this.state.user_id,
            user_email: this.state.user_email,
            user_email_verified: this.state.user_email_verified,
            user_fullname: this.state.user_fullname,
            user_username: this.state.user_username,
            user_dp: this.state.user_dp,
            user_bio: this.state.user_bio,
            user_account_type: this.state.user_account_type,
            user_gender: this.state.user_gender,
        }

        if (cachify) {
            window.localStorage.setItem("auth", JSON.stringify(Auth))
            window.localStorage.setItem("cache-update", Date.now())
        }
    }

    setAuth(data) {
        if (data.success) {
            this.setState({
                isLoggedIn: true,
                auth_token: data.message.token,
                user_id: data.message.user.user_id,
                user_email: data.message.user.email,
                user_email_verified: data.message.user.email_verified,
                user_fullname: data.message.user.details.fullname,
                user_username: data.message.user.details.username,
                user_dp: data.message.user.details.profile_image_link,
                user_bio: data.message.user.details.user_bio,
                user_account_type: data.message.user.details.account_type,
                user_gender: data.message.user.details.gender,
            })
        }
    }

    removeAuth() {
        window.localStorage.removeItem("auth")

        if (cachify) {
            window.localStorage.setItem("cache-update", Date.now())
        }

        this.setState({
            isLoggedIn: false,
            auth_token: "",
            user_id: "",
            user_email: "",
            user_email_verified: 0,
            user_fullname: "",
            user_username: "",
            user_dp: "",
            user_bio: "",
            user_account_type: 0,
            user_gender: -1,
        })

        Axios.post(`${getApiLink()}/logout`, {
            xAuthToken: this.state.auth_token
        }).then((response) => {
            notyf.success("Logged out successfully!")
        }).catch((e) => {
            notyf.error("Error occured while logging out " + e.response)
        })

    }
    render() {

        let { keys, setItem, getItem, getStorage } = this.context
        // let key2 = randomizeKeys()
        // console.log(keys)
        // secureStorage.genera
        // let k = generateNewKeys()
        console.log({ keys })
        // let keys = {
        //     private_key: k.privateKey,
        //     public_key: k.publicKey
        // }
        setItem("hey", "1234 this is new with context")
        let h = getItem("hey")
        let s = getStorage()

        console.log({ h, s })

        return (
            <AuthContext.Provider value={{ ...this.state, setAuth: this.setAuth, removeAuth: this.removeAuth }}>
                {this.props.children}
            </AuthContext.Provider>
        );
    }
}

export default AuthContextProvider;
