import React from 'react';

import Axios from "axios"
// import DropdownMenu, { DropdownItem } from '../components/dropdown'
import { getApiLink } from "../lib/env.lib"
const getStatusDisp = (disp) => {
    let d;
    if (disp === -1) {
        d = (<> <span style={{ color: "yellow" }}>LODING</span></>)
    } else if (disp === 0) {
        d = (<> <span style={{ color: "red" }}>INACTIVE</span></>)
    } else if (disp === 1) {
        d = (<> <span style={{ color: "green" }}>ACTIVE</span></>)
    }

    return d
}
const Status = (props) => {

    const [uiStatus, setUiStatus] = React.useState(-1)
    const [ver, setVer] = React.useState("")
    const [build_ver, setBuildVer] = React.useState("")
    const [build_time, setBuildTime] = React.useState("")

    let codeStyle= {
        fontSize: "16px",
        color: "#35c644"
    }
    React.useEffect(() => {
        console.log({ api: getApiLink() })
        Axios.get(`${getApiLink()}/api/v1/user-api-service`).then((response) => {
            console.log(response)
            if (response.data.success) {
                setUiStatus(1)
            } else {
                setUiStatus(0)
            }
        })

        Axios.get(`${getApiLink()}/`).then((response) => {
            console.log(response)
            if (response) {
                setVer(response.data.version)
            } else {
                setVer("Loding")
            }
        })

        Axios.get("/version.json").then((response) => {
            console.log(response)
            if (response) {
                setBuildVer(response.data.version)
                setBuildTime(new Date(response.data.build).toISOString())
            }
        }).catch((e) => {
            setBuildVer("")
            setBuildTime("")
            console.log(e)
        })

    }, [])
    return (
        <>
            <div className="container">

                <div className="w-100 text-center mt-4">
                    <h1>Lattice</h1>
                </div>
                <p> UI Build Version: <code style={codeStyle}>{build_ver}</code> - <code style={codeStyle}>{build_time}</code></p>
                <p>Server Build Version: <code style={codeStyle}>{ver}</code></p>

                <h1>Status</h1>

                <p>UI STATUS: {getStatusDisp(uiStatus)}</p>

                {/* <DropdownMenu title={"hey"} className="btn btn-primary">
                    <DropdownItem type="link" to="/settings">
                        settings
                    </DropdownItem>
                    <DropdownItem onClick={(()=>{alert("hey")})}>
                        hey2
                    </DropdownItem> 
                    <DropdownItem>
                        hey3
                    </DropdownItem>
                     <DropdownItem>
                        hey4
                    </DropdownItem>
                    <hr className="light p-0 m-0" />
                    <DropdownItem>
                        hey
                    </DropdownItem>
                </DropdownMenu> */}
            </div>
        </>
    );
}

export default Status;