import React, { useState, useContext } from 'react';

import { Link } from "react-router-dom"

import parser from 'html-react-parser';
import { Modal } from "react-bootstrap"
import Axios from 'axios'

import DropdownMenu, { DropdownItem } from './dropdown'
import { hexStrColor, invertColor } from "../lib/colors.lib"
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faCommentAlt, faBookmark, faThumbsUp } from '@fortawesome/free-regular-svg-icons'
import { faEllipsisV, faThumbsUp as solidUp, faBookmark as solidBookmark } from '@fortawesome/free-solid-svg-icons'
import Loader from "./loader"
import { CreateRounded, DeleteRounded } from '@material-ui/icons'
import { AuthContext } from "../contexts/auth_context"

function formatDate(ts) {

    let dt = new Date(ts)
    let day = dt.getDay()
    let date = dt.getDate()
    let month = dt.getMonth()
    let year = dt.getFullYear()
    let week = ["Mon", "Tue", "Wed", "Thu", "Fri", "Sat"]
    let mon = ["Jan", "Feb", "Mar", "Apr", "May", "Jun", "Jul", "Aug", "Sep", "Oct", "Now", "Dec"]
    return `${week[day - 1]} ${date} ${mon[month]} ${year}`

}
function setLikeDisp(liked) {
    let likeDisp
    if (!liked) {
        likeDisp = (
            <FontAwesomeIcon icon={faThumbsUp} className="action-icon" />
        )
    } else {
        likeDisp = (
            <FontAwesomeIcon icon={solidUp} className="action-icon" />
        )
    }

    return likeDisp;
}

function setBookmarkDisp(bookmarked) {
    let bkDisp
    if (!bookmarked) {
        bkDisp = (
            <FontAwesomeIcon icon={faBookmark} className="action-icon" />
        )
    } else {
        bkDisp = (
            <FontAwesomeIcon icon={solidBookmark} className="action-icon" />
        )
    }

    return bkDisp;
}

function deleteArticle(id) {
    if (id) {
        Axios.delete(`/delete/${id}`)
    }
}

const UserDetails = (props) => {

    const [liked, setLiked] = useState(false)
    const [bookmarked, setBookmarked] = useState(false)

    const [modalDisp, setModalDisp] = useState(false)

    console.log({ p_from_card: props })
    let bookmarkDisp = setBookmarkDisp(bookmarked)
    let likeDisp = setLikeDisp(liked)

    function handleLike(e) {
        setLiked(!liked)
        likeDisp = setLikeDisp(liked)
    }

    function handleBookmark(e) {
        setBookmarked(!bookmarked)
        bookmarkDisp = setBookmarkDisp(bookmarked)
    }

    const { user_id } = useContext(AuthContext)

    return (
        <>
            <div className="user-info-c pl-2 pr-2 pb-2">
                <div className="user-image-c hide-xs_x show-sm_x pr-2">
                    <Link className="unset" to={`/@${props.data.user.username}`}>
                        <img src={props.data.user.imageLink} alt="user" className="user-image" style={{
                            backgroundColor: "white"
                        }} />
                    </Link>
                </div>

                <div className="user-info">
                    <Link className="unset" to={`/@${props.data.user.username}`}>
                        <span className="u-name d-block">{props.data.user.name}</span>
                        <span className="u-username d-inline-block">@{props.data.user.username}</span>
                    </Link>
                </div>

                <div className="ml-auto u-actions">
                    <span className="action-icon-c text-center_x" onClick={handleBookmark}>
                        {bookmarkDisp}
                    </span>
                    <span className="action-icon-c text-center_x">
                        <FontAwesomeIcon icon={faCommentAlt} className="action-icon" />
                    </span>
                    <span className="action-icon-c text-center_x mr-4x" onClick={handleLike}>
                        {likeDisp}
                    </span>
                    {/* <span className="action-icon-c text-center_x">
                        <FontAwesomeIcon icon={faEllipsisV} className="action-icon" />
                    </span> */}

                    <DropdownMenu title={<FontAwesomeIcon icon={faEllipsisV} className="" />}
                        autoClose={true}
                        className="drop-btn dropdown-toggle_x btn"
                        containerClassName="action-icon-c last mr-4"
                        menuStyle={{
                            transform: (window.innerWidth <= 576) ? "translateX(-60%)" : null
                        }}>
                        {/* <DropdownItem type="link" to="/settings">
                            Settings
                        </DropdownItem> */}
                        {/* <hr className="light p-0 m-0" /> */}


                        {(() => {
                            if (user_id === props.data.user.user_id) {
                                return (<>
                                    <DropdownItem icon={<CreateRounded />} type="link" to={`/edit/${props.data.article_id}`}>Edit</DropdownItem>
                                    <hr className="light p-0 m-0" />
                                    <DropdownItem className="logout" icon={<DeleteRounded />} onClick={(() => { setModalDisp(true) })}>Delete</DropdownItem>                                </>)
                            } else {
                                return (<>
                                    <DropdownItem>Report</DropdownItem>
                                </>)
                            }
                        })()}

                    </DropdownMenu>


                    <Modal show={modalDisp}
                        onHide={(() => { setModalDisp(false) })}
                    >
                        <Modal.Header closeButton>
                            <Modal.Title>Delete Article</Modal.Title>
                        </Modal.Header>
                        <Modal.Body>
                            Are you sure you want to delete this article?
                            <br />
                        </Modal.Body>
                        <Modal.Footer>
                            <button className="btn btn-secondary" onClick={(() => { setModalDisp(false) })}>Close</button>
                            <button className="btn btn-danger" onClick={props.hideCard}>Delete</button>
                        </Modal.Footer>
                    </Modal>
                    {/* <Dropdown className="action-icon-c last text-center_x mr-4">
                        <Dropdown.Toggle className="drop-btn" variant="info">
                            <FontAwesomeIcon icon={faEllipsisV} className="action-icon nav-btn" />
                        </Dropdown.Toggle>

                        <Dropdown.Menu>
                            {(() => {
                                if (user_id == props.data.user.user_id) {
                                    return (<>
                                        <Dropdown.Item as={Link} to={`/edit/${props.data.article_id}`}>Edit</Dropdown.Item>
                                    </>)
                                } else {
                                    return (<>
                                        <Dropdown.Item eventKey="2">Report</Dropdown.Item>
                                    </>)
                                }
                            })()}


                        </Dropdown.Menu>
                    </Dropdown> */}
                </div>
            </div>
        </>
    );
}

const Card = (props) => {
    console.log(props)

    const [hideMode, setHideMode] = useState(false)
    function trimContent(c) {
        if (c) {
            if (c.length > 200)
                c = c.slice(0, 200) + "..."
        } else {
            return ""
        }

        return c
    }

    let style = {
        display: "none"
    }
    // };
    if (props.data) {
        if (props.data.bannerLink != null && props.data.bannerLink !== undefined && props.data.bannerLink !== "NA") {
            style = {
                backgroundImage: `url(${props.data.bannerLink})`,
                backgroundSize: "cover",
                // backgroundPosition: "50% 50%",
                // backgroundOverflow: "visible",
                backgroundPosition: "50% 50%",
            }
        }
    }


    if (props.type >= 1 && props.type <= 3) {
        if (hideMode) {
            return (<></>)
        } else
            return (
                <>
                    <div className="custom-card2 mb-3">

                        <div className="row">

                            <div className={(() => {
                                if (props.data.bannerLink !== undefined && props.data.bannerLink !== null && props.data.bannerLink !== "NA")
                                    return "col-md-3 col-lg-3"
                                else
                                    return "col-md-12"
                            })()}>
                                <div className="img hide-xs hide-sm show-md" style={{
                                    ...style,
                                    backgroundRepeat: "no-repeat",
                                    height: "100%",
                                    borderRadius: "18px"
                                }}></div>
                            </div>

                            <div className={(() => {
                                if (props.data.bannerLink !== undefined && props.data.bannerLink !== null && props.data.bannerLink !== "NA")
                                    return "col-md-9 col-lg-9"
                                else
                                    return "col-md-12"
                            })()}
                            >
                                <Link className="unset" style={{
                                    color: "unset"
                                }} to={`/read/${props.data.article_id}`}>

                                    <div style={{
                                        cursor: "pointer"
                                    }}
                                        className={(() => {
                                            if (props.data.bannerLink !== undefined && props.data.bannerLink !== null && props.data.bannerLink !== "NA")
                                                return "pl-2"
                                            else
                                                return "pl-2 pr-2"
                                        })()}>
                                        <div className="article-row">
                                            <div className="pt-3 pb-2">
                                                <div className="article-date">
                                                    {formatDate(props.data.time)}
                                                </div>
                                                <div className="article-title">
                                                    {parser(props.data.title)}
                                                </div>
                                            </div>
                                            <div className="article-image show-xs show-sm hide-md">
                                                <div className="img" style={style}></div>
                                            </div>
                                        </div>


                                        <div className="article-content pb-2" style={{
                                            // maxHeight: "80px",
                                            // overflow: "hidden",
                                            // textOverflow: "ellipsis"
                                        }}>
                                            {trimContent(parser(props.data.content.split("<br>").join("")))}
                                        </div>
                                    </div>
                                </Link>

                                <div className="tags pl-2" style={{
                                    paddingBottom: "3px",
                                }}>
                                    {(() => {
                                        if (props.data.tags)
                                            return props.data.tags.map(i => {
                                                let h = hexStrColor(i)
                                                let inv = invertColor(h, true)

                                                return (<>
                                                    <div className="tag d-inline-block noselect" style={{
                                                        backgroundColor: "#" + h,
                                                        color: inv,
                                                        cursor: "pointer"
                                                    }}>
                                                        #{i}
                                                    </div>
                                                </>)
                                            })
                                    })()}

                                </div>
                                {/* <div className={(() => {
                                if (props.data.bannerLink !== undefined && props.data.bannerLink !== null && props.data.bannerLink !== "NA")
                                    return "pl-2 pb-2"
                                else
                                    return "pl-4 pr-42 pb-2"
                            })()}> */}
                                <UserDetails data={props.data} hideCard={(() => { setHideMode(true) })} />
                                {/* </div> */}
                            </div>

                        </div>

                    </div>
                    {/* <hr className="light" /> */}
                </>
            );
    } else if (props.type == -1) {
        return (<></>)
    } else {
        return (
            <>
                <div className="card-loader w-100 text-center">
                    <br />
                    <br />
                    <br />
                    <Loader />
                    <br />
                    <br />
                    <br />
                </div>
                {/* <hr className="light"/> */}
            </>
        )
    }
}

export default Card;