import React, { useState } from 'react'
import { Link } from 'react-router-dom'

const Dropdown = (props) => {
    const [open, setOpen] = useState(false)


    return (
        <>
            <div className={`cs-dropdown-container ${(props.containerClassName) ? props.containerClassName : ""}`}
                onMouseLeave={() => { if (props.autoClose) setOpen(false) }}
            // onTouchEnd={() => { if (props.autoClose) setOpen(false) }}
            >

                <div className={`cs-dropdown-btn ${(props.className) ? props.className : ""} ${(props.addActiveClass && open) ? "active" : ""}`} onClick={() => { setOpen(!open) }} style={{
                    ...props.style
                }}>
                    {props.title}
                </div>

                {(() => {
                    if (open && props.children) {
                        return (
                            <>
                                <div className={`cs-dropdown-menu ${(props.menuClassName) ? props.menuClassName : ""}`} onClick={() => { setOpen(!open) }} style={{
                                    ...props.menuStyle
                                }}>
                                    {props.children}
                                </div>
                            </>
                        )
                    }
                })()}
            </div>
        </>
    );
}

export const DropdownItem = (props) => {

    if (props.type == "link") {
        return (
            <>
                <Link to={props.to} className="unset">
                    <div className={`cs-dropdown-item ${(props.className) ? props.className : ""}`} style={{
                        ...props.style,
                        paddingLeft: (props.icon) ? "2px" : "18px"
                    }}
                    >
                        <span style={{ fontSize: "16px", paddingRight: (props.icon) ? "6px" : "0px" }}>
                            {props.icon}
                        </span>
                        {props.children}
                    </div>
                </Link>
            </>
        );
    } else {
        return (
            <>
                <div className={`cs-dropdown-item ${(props.className) ? props.className : ""}`} style={{
                    ...props.style,
                    paddingLeft: (props.icon) ? "2px" : "18px"
                }}
                    onClick={(props.onClick) ? props.onClick : null}
                >
                    <span style={{ fontSize: "16px", paddingRight: (props.icon) ? "6px" : "0px" }}>
                        {props.icon}
                    </span>
                    {props.children}
                </div>
            </>
        );
    }
}

export default Dropdown;